<template>
  <div class="user-profile">
    <!--  -->
    <van-nav-bar title="" left-arrow right-text=""
    @click-left="$router.back()"
    @click-right="$router.back()"></van-nav-bar>
    <!--  -->
    <van-cell-group>
      <van-cell is-link title="" value="" @click="onSelectFile">
        <van-image
          class="avatar"
          round
          :src="user.photo"
        />
      </van-cell>
      <input type="file" hidden ref="file" @change="onFileChange" />
      <van-cell is-link title=""
        :value="user.name"
        @click="isEditNameShow = true"/>
      <van-cell is-link title="" value="" />
      <van-cell is-link title=""
      :value="user.gender === 0? '': ''"
      @click="isEditGenderShow= true"/>
      <van-cell is-link title=""
      :value="user.birthday"
      @click="isEditBirthdayShow= true" />
    </van-cell-group>
    <!--  -->
    <van-popup
      v-model="isEditNameShow"
      position="bottom"
    >
      <van-nav-bar
        title=""
        left-text=""
        right-text=""
        @click-left="isEditNameShow = false"
        @click-right="onUpdateName"
      />
      <van-field
        :value="user.name"
        @input="inputName = $event"
        rows="2"
        autosize
        type="textarea"
        maxlength="20"
        placeholder=""
        show-word-limit
      />
    </van-popup>
    <!--  -->
    <van-action-sheet
      v-model="isEditGenderShow"
      :actions="actions"
      cancel-text=""
      @cancel="isEditGenderShow = false"
      @select="onGenderSelect"
    />
    <!--  -->
     <van-popup
      v-model="isEditBirthdayShow"
      position="bottom"
    >
      <!--
        v-model="currentDate" 
        :min-date="minDate" 
        max-date  
       -->
      <van-datetime-picker
        :value="currentDate"
        type="date"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="isEditBirthdayShow = false"
        @confirm="onUpdateBirthday"
      />
     </van-popup>
  </div>
</template>

<script>
import {
  getUserProfile,
  updateUserPhoto,
  updateUserProfile } from '@/api/user'
import { ImagePreview } from 'vant'
import moment from 'moment'
export default {
  name: 'user-profile',
  data () {
    return {
      user: {},
      isEditNameShow: false, // 
      inputName: '', // 
      isEditGenderShow: false, // 
      actions: [
        // name value 
        { name: '', value: 1 },
        { name: '', value: 0 }
      ],
      isEditBirthdayShow: false,
      minDate: new Date(1970, 0, 1),
      maxDate: new Date()
    }
  },
  computed: {
    file () {
      return this.$refs['file']
    },
    currentDate () {
      return new Date(this.user.birthday)
    }
  },
  created () {
    this.loadUserProfile()
  },
  methods: {
    //    
    //  field, value
    async updateMessage (field, value) {
      this.$toast.loading({
        duration: 0, //  toast
        message: '...',
        forbidClick: true // 
      })
      try {
        await updateUserProfile({
          [field]: value
        })
        this.$toast.success('')
      } catch (err) {
        this.$toast.fail('')
      }
    },
    // 
    async onUpdateBirthday (value) {
      // moment
      const date = moment(value).format('YYYY-MM-DD')
      // 
      await this.updateMessage('birthday', date)

      // 
      this.user.birthday = date
      // 

      this.isEditBirthdayShow = false
    },
    // 
    async onGenderSelect (data) {
      // 
      await this.updateMessage('gender', data.value)
      // 
      this.user.gender = data.value
      // 
      this.isEditGenderShow = false
    },
    // 
    async onUpdateName () {
      // 
      await this.updateMessage('name', this.inputName)

      // 
      this.user.name = this.inputName

      // 
      this.isEditNameShow = false
    },
    // van-cellinput
    onSelectFile () {
      this.file.click()
    },
    // 
    async loadUserProfile () {
      try {
        const { data } = await getUserProfile()
        this.user = data.data
      } catch (error) {
        console.log(error)
        this.$toast.fail('')
      }
    },
    onFileChange () {
      const fileData = URL.createObjectURL(this.file.files[0]) //   
      ImagePreview({
        images: [fileData],
        startPosition: 1,
        onClose: this.savePhoto
      })
    },
    // 
    async savePhoto () {
      await this.$dialog.confirm({
        title: '',
        message: ''
      })
      this.$toast.loading({
        duration: 0, //  toast
        message: '...',
        forbidClick: true // 
      })
      try {
        //   fromdata 
        const fd = new FormData()
        // append
        fd.append('photo', this.file.files[0])
        // 
        await updateUserPhoto(fd)
        //  userphoto  
        this.user.photo = URL.createObjectURL(this.file.files[0])
        this.$toast.success('')
      } catch (error) {
        console.log(error)
        this.$toast.fail('')
      }
    }
  }
}
</script>

<style scoped lang="less">
.avatar {
  width: 30px;
  height: 30px;
}
/deep/ .van-nav-bar{
  background-color: #fff;
  .van-nav-bar__title{
    color: #323233
  }
}
</style>
